*, :before, :after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

:root {
  --body-colour: #8bc6ec;
  --main-colour: #d6e6f0;
  --dark-colour: #415c6e;
  --light-red: #eda4a4;
  --bright-red: #f74e4e;
}

html {
  box-sizing: border-box;
  font-family: Poppins, sans-serif;
  font-size: 100%;
}

body {
  background-color: var(--body-colour);
}

.container {
  background-color: var(--main-colour);
  width: 90%;
  border-radius: 5px;
  margin: 20px auto 0;
  padding: 10px;
}

.todo-top h2, .todo-bottom h2 {
  color: var(--dark-colour);
  font-weight: 700;
}

.input-group {
  margin: 2rem 0 1rem;
  position: relative;
}

.input-group > label {
  color: #ababab;
  transition: transform .15s ease-out;
  position: absolute;
  left: 5px;
}

.input-group > input {
  width: 100%;
  border: none;
  border-bottom: 3px solid var(--dark-colour);
  background-color: inherit;
  border-radius: 2.8px;
  padding: 5px;
}

.input-group > input:focus {
  outline: none;
}

.todo-top > button {
  width: 25%;
  height: 30px;
  color: var(--dark-colour);
  background-color: var(--body-colour);
  cursor: pointer;
  border: none;
  border-radius: 3px;
  margin-bottom: 1.5rem;
  padding: 5px;
  font-size: 1rem;
  font-weight: 700;
  box-shadow: 0 1.5px 4px #757575;
}

hr {
  background-color: var(--dark-colour);
  height: 1px;
  border: none;
}

.todo-bottom {
  margin-top: 1rem;
}

.todo-bottom h2 {
  margin-bottom: 1rem;
}

.todo-list {
  list-style-type: none;
}

.todo-list li {
  border: 1px solid var(--dark-colour);
  cursor: pointer;
  border-bottom: none;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  display: flex;
}

.todo-list li > p:hover {
  color: gray;
  transition: all .4s ease-in-out;
}

.todo-list li:last-of-type {
  border-bottom: 1px solid var(--dark-colour);
}

.todo-list div {
  width: 30px;
  height: 30px;
  cursor: pointer;
  position: relative;
}

.todo-list span {
  height: 5px;
  width: 20px;
  background-color: var(--light-red);
  border-radius: 5px;
  display: block;
  position: absolute;
  top: 13px;
  left: 5px;
}

.todo-list li > div:hover > span {
  background-color: var(--bright-red);
}

.line1 {
  transform: rotate(-45deg)translateY(0);
}

.line2 {
  transform: rotate(45deg)translateY(0);
}

.focused {
  font-size: .8rem;
  transform: translateY(-125%);
}

@media only screen and (max-width: 330px) {
  .todo-top > button {
    width: 100% !important;
  }
}

@media only screen and (max-width: 393px) {
  .todo-top > button {
    height: 40px;
    width: 30%;
  }
}

@media only screen and (min-width: 768px) {
  .container {
    width: 70%;
  }

  .todo-top > button {
    height: 40px;
    width: 20%;
  }
}

